.modal {
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    z-index: 1000;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7);
  }
  
  .modal-content {
    background-color: #fff;
    padding: 20px;
    border-radius: 10px;
    width: 80%;
    max-width: 900px;
    position: relative;
  }
  
  .close {
    position: absolute;
    top: 10px;
    right: 20px;
    font-size: 30px;
    cursor: pointer;
    color: black;
    background-color: transparent;
    border: none;
    padding: 10px; /* Added padding to increase the clickable area */
    z-index: 2; /* Make sure it's on top of other elements */
  }
  
  .close:hover {
    color: whitesmoke; /* Optional: change color on hover to indicate it's clickable */
  }
  
  
  .slick-slide {
    padding: 0 15px; /* Adds space between videos */
  }
  
  video {
    cursor: pointer;
  }
  @media (max-width: 768px) {
    .modal-content {
        width: 90%;  /* Wider on small screens */
        padding: 15px; /* Reduce padding */
    }

    .close {
        font-size: 25px; /* Slightly smaller close button */
    }
